import React, { Component } from "react";
import Select from "react-select";
import data from "./data.json";
import Table from "./table/table";
import "./App.css";

console.log("data", data);
const mentors = data[1].map(el => {
    let val = el.github.slice(el.github.lastIndexOf("/") + 1);
    return { value: val, label: val };
});

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            students: [],
            selectedOption: null,
            tasks: data[0].map(el => {
                return { task: el.task, link: el.link, status: el.status };
            })
        };
    }

    handleChange = selectedOption => {
        this.students = data[1].filter(
            el =>
                el.github.slice(el.github.lastIndexOf("/") + 1) ===
                selectedOption.value
        );
        this.students = this.students[0];
        this.a = this.students.students;
        this.setState({ selectedOption });
        this.nameMentor = selectedOption.value;
        this.setState({ students: this.a });
        this.stud = this.a.map(el =>
            el.studentGithub.slice(el.studentGithub.lastIndexOf("/") + 1)
        );
    };

    render() {
        const { selectedOption } = this.state;

        return (
            <div className="App">
                <Select
                    value={selectedOption}
                    onChange={this.handleChange}
                    options={mentors}
                />
                <p>{this.nameMentor} </p>
                <Table
                    items={this.stud || []}
                    cv={this.state.tasks}
                    st={this.students || []}
                    studnt = {this.a || []}
                />

                <div>{this.str}</div>
            </div>
        );
    }
}

export default App;
