import React, { Component } from "react";
import "./table.css";

class Table extends Component {
    render() {
        let score = [];
        score = this.props.studnt.map(el => el.marks);
        return (
            <div>
                <table>
                    <thead>
                        <tr>
                            <th>Students/Tasks</th>
                            {this.props.items.map((el, i) => (
                                <th key={i}>
                                    <a href={"https://github.com/" + el}>
                                        {el}
                                    </a>
                                </th>
                            ))}
                        </tr>
                    </thead>

                    <tbody>
                        {this.props.cv.map((el, i) => (
                            <tr key={i}>
                                <td className={el.status.toLowerCase()}>
                                    <a href={el.link}>{el.task}</a>
                                </td>
                                {score.map((el, j) => (
                                    <td
                                        key={j}
                                        className={el[i] ? "done" : null}
                                    />
                                ))}
                            </tr>
                        ))}
                    </tbody>
                </table>
                <legend>
                    <div className="legend-item">
                        <span className="red" /> time for checking is gone and no mark from mentor
                    </div>
                    <div className="legend-item">
                        <span className="rose" /> need to check
                    </div>
                    <div className="legend-item">
                        <span className="yellow" /> students working on that task now
                    </div>
                    <div className="legend-item">
                        <span className="green" /> checked by mentor
                    </div>
                    <div className="legend-item">
                        <span className="grey" /> task in todo state
                    </div>
                </legend>
            </div>
        );
    }
}

export default Table;
